import { APP } from "../../shared/app/APP";
import { sortEventos } from "../../shared/services/sortEventos";
import { titles as titlesEventos } from "../../shared/app/Titles";
import { storage } from "../../shared/utils/storage";
import { GitHubRepository } from "../../shared/utils/git";
import { Evento } from "../../shared/app/Eventos";
import { reactive } from "vue";

export const app = <APP>reactive(new APP("APP", titlesEventos));

const githubRepo = new GitHubRepository(
  "ccbfaina",
  "ccbfaina.github.io",
  localStorage.getItem("key") || ""
);

// Salvar dados
app.onSaveAllEvents(() => {
  try {
    console.log("salvando todos os dados::: ", app.eventos.values.length);
    sortEventos(app);
    storage.saveItem("offlineEventsData", app.eventos.values);
    storage.saveItem("eventsData", app.eventos.values);
    const saveGit = async () => {
      try {
        if (navigator.onLine && app.eventos.values.length) {
          const result = await githubRepo.commitFile(
            "data/events.json",
            JSON.stringify(app.eventos.values)
          );
          if (result && result.ok) {
            console.log(`Dados atualizados no Git com status ${result.status}`);
            storage.removeItem("offlineEventsData");
          }
        } else {
          alert("Você está offline. Conecte-se à internet e tente novamente!");
        }
      } catch (e) {
        console.info("Erro ao atualizar dados no servidor git!");
      }
    };
    saveGit();
  } catch (error) {
    console.log("Error: ", error);
  }
});
// Carregar dados
function load(eventos: Evento[]) {
  if (Array.isArray(eventos)) {
    app.eventos.values = [];
    eventos.map((e) => app.eventos.add(e));
    storage.saveItem("eventsData", app.eventos.values);
    sortEventos(app);
  }
}

// Carregar dados de cache
const data = storage.getItem("eventsData", []);
load(data);

// Carregar dados da internet
async function loadInternet() {
  const offline = storage.getItem("offlineEventsData", []);
  if (offline.length) {
    console.log("Ha dados offline para salvar!!!");
    app.saveAllEvents();
    return;
  }

  if (app.isAdmin) {
    const res = await githubRepo.getContentData("data/events.json");
    if (res?.content) {
      load(res.content);
    }
  } else {
    const response = await fetch(
      `/data/events.json?v=${new Date().toISOString().replace(/\D/g, "")}`,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    if (response.ok) {
      const json = await response.json();
      load(json);
    }
  }
}
loadInternet();

// Quando volta de offline, recarrega os dados!
app.onLine((status) => {
  if (status) loadInternet();
});
