import { Evento } from "../../shared/app/Eventos";
import { GitHubRepository } from "../../shared/utils/git";

export const githubRepo = new GitHubRepository(
  "ccbfaina",
  "ccbfaina.github.io",
  localStorage.getItem("key") || ""
);

export const saveEvento = {
  eventos: [] as Evento[],
  sync() {
    if (navigator.onLine && saveEvento.eventos.length) {
      githubRepo.commitFile("data/events.json", JSON.stringify(this.eventos));
      console.log("Enviar dados para o servidor::: ", this.eventos);
      this.store([]);
    }
  },
  store(eventos: Evento[]): void {
    this.eventos = eventos;
    localStorage.setItem("offlineEvents", JSON.stringify(eventos));
  },
};

saveEvento.eventos = JSON.parse(localStorage.getItem("offlineEvents") || "[]");
setInterval(saveEvento.sync, 5000);
