import { reactive } from "vue";
import { v4 as uuidv4 } from "uuid";
import { storage } from "../utils/storage";
import { titles } from "./Titles";
import { saveEvento } from "../../web/composables/apiService";
import { GitHubRepository } from "../utils/git";

export const githubRepo = new GitHubRepository(
  "ccbfaina",
  "ccbfaina.github.io",
  localStorage.getItem("key") || ""
);

export interface Evento {
  id: string;
  list: string;
  title: string;
  locale: string;
  desc: string;
  date?: Date;
  end?: Date;
  gid?: string;
  updated?: Date;
  recurring?: boolean;
  maps?: string;
}

export interface Repository {
  selected: Evento | null;
  all(): Evento[];
  find(id: string, date: Date): Evento | undefined;
  save(source: Evento): Evento;
  clear(): void;
}

class MemoryRepository implements Repository {
  private events: Evento[] = [];
  selected: Evento | null = null;

  all(): Evento[] {
    return this.events;
  }

  find(id: string, date: Date): Evento | undefined {
    return this.events.find(
      (e) => e.id === id && e.date?.getTime() === date.getTime()
    );
  }

  save(source: Evento): Evento {
    this.ensureEventId(source);
    this.normalizeTitle(source);

    if (this.selected?.id && this.selected.date) {
      const evento = this.find(this.selected.id, this.selected.date);
      if (evento) {
        this.updateEventIfNecessary(evento, source);
        this.sortEvents();
        return evento;
      }
    }

    this.addNewEvent(source);
    this.sortEvents();
    return source;
  }

  private ensureEventId(event: Evento): void {
    if (!event.id) {
      event.id = uuidv4();
    }
  }

  private normalizeTitle(event: Evento): void {
    if (event.title) {
      event.title = event.title.toUpperCase();
    }
  }

  private updateEventIfNecessary(
    existingEvent: Evento,
    newEvent: Evento
  ): void {
    const isUpdate =
      (!existingEvent.updated && newEvent.updated) ||
      (existingEvent.updated &&
        newEvent.updated &&
        existingEvent.updated < newEvent.updated);

    if (isUpdate) {
      Object.assign(existingEvent, newEvent);
    }
  }

  private addNewEvent(event: Evento): void {
    this.events.push(event);
  }

  private sortEvents(): void {
    this.events.sort((a, b) => {
      const titleA = titles.indexOf(a.title?.toUpperCase() || "");
      const titleB = titles.indexOf(b.title?.toUpperCase() || "");

      if (titleA !== titleB) {
        return titleA - titleB;
      }

      if (b.date && a.date && b.date.getTime() !== a.date.getTime()) {
        return a.date.getTime() - b.date.getTime();
      }

      if (a.locale && b.locale) {
        return a.locale.localeCompare(b.locale);
      }

      return 0;
    });
  }

  clear(): void {
    this.events = [];
  }
}

class EventApplication {
  constructor(private readonly repo: Repository = new MemoryRepository()) {}

  get selected(): Evento | null {
    return this.repo.selected;
  }

  set selected(event: Evento | null) {
    this.repo.selected = event;
  }

  save(source: Evento) {
    try {
      if (this.shouldRemoveSelected(source)) {
        this.removeSelected();
        return this.repo.save({ ...source, id: "" });
      } else {
        return this.repo.save(source);
      }
    } catch (error) {
      console.log(error);
    }
  }

  all(): Evento[] {
    return this.repo.all();
  }

  json() {
    return JSON.stringify(this.all());
  }

  remove(event: Evento) {
    this.markAsRemoved(event);
    return this.repo.save(event);
  }

  find(event: Evento) {
    if (event.date) {
      return this.repo.find(event.id, event.date);
    }
  }

  reload() {
    console.log("Recarregar eventos do servidor!!!");
  }

  async init() {
    this.loadCachedEvents();
    const eventData = await this.loadEventData();
    if (eventData && eventData.length) {
      this.replaceEvents(eventData);
      this.storageSave();
    }
  }

  storageSave() {
    storage.saveItem("events", this.all());
  }

  private async gitSave() {
    try {
      const newEvents = this.all();
      const oldEvents = await this.loadEventData();
      this.mergeNewWithOldEvents(newEvents, oldEvents);
      this.storageSave();
      saveEvento.eventos = newEvents;
    } catch (error) {
      alert("Erro ao salvar item!!!");
    }
  }

  private shouldRemoveSelected(source: Evento): boolean {
    return !!(
      this.selected &&
      this.selected.date &&
      this.selected.date.toISOString() !== source.date?.toISOString()
    );
  }

  private removeSelected(): void {
    if (this.selected) {
      this.selected.desc = "REMOVE";
      this.selected = null;
    }
  }

  private markAsRemoved(event: Evento): void {
    event.desc = "REMOVE";
  }

  private loadCachedEvents(): void {
    const cache = storage.getItem("events");
    if (cache && cache.length) {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      cache.forEach((e: any) => {
        this.save(this.prepareEvent(e));
      });
    }
  }

  private async loadEventData(): Promise<Evento[]> {
    let data: Evento[] = [];
    try {
      const admin = localStorage.getItem("key");
      if (admin) {
        const res = await githubRepo.getContentData("data/events.json");
        if (res?.content) data = res.content;
      } else {
        const response = await fetch(
          `/data/events.json?v=${new Date().toISOString().replace(/\D/g, "")}`,
          {
            method: "GET",
            headers: {
              "Content-Type": "application/json",
            },
          }
        );
        if (response.ok) {
          data = await response.json();
        }
      }
    } catch (error) {
      console.log("Erro ao carregar dados:", error);
    }
    return data;
  }

  private prepareEvent(eventData: Partial<Evento>): Evento {
    return {
      id: eventData.id || uuidv4(),
      list: eventData.list || "",
      title: eventData.title || "",
      locale: eventData.locale || "",
      desc: eventData.desc || "",
      date: eventData.date ? new Date(eventData.date) : undefined,
      end: eventData.end ? new Date(eventData.end) : undefined,
      updated: eventData.updated ? new Date(eventData.updated) : undefined,
      gid: eventData.gid,
      recurring: eventData.recurring,
    };
  }

  private replaceEvents(data: Evento[]): void {
    this.repo.clear();
    data.forEach((e: Evento) => {
      this.save(this.prepareEvent(e));
    });
  }

  private mergeNewWithOldEvents(
    newEvents: Evento[],
    oldEvents: Evento[]
  ): void {
    newEvents.forEach((eventNew) => {
      const old = oldEvents.find(
        (old) =>
          old.id === eventNew.id &&
          old.updated &&
          eventNew.updated &&
          old.updated > eventNew.updated
      );
      if (old) Object.assign(eventNew, old);
    });
  }
}

export const eventApp = reactive(new EventApplication());

eventApp.init();
