import { APP } from "../app/APP";
import { Evento } from "../app/Eventos";

export function sortEventos(app: APP): Evento[] {
  if (!app.eventos.values.length) return app.eventos.values;
  app.eventos.values.sort((a, b) => {
    const titleA = app.titles.indexOf(a.title?.toUpperCase() || "");
    const titleB = app.titles.indexOf(b.title?.toUpperCase() || "");

    if (titleA !== titleB) {
      return titleA - titleB;
    }

    if (b.date && a.date && b.date.getTime() !== a.date.getTime()) {
      return a.date.getTime() - b.date.getTime();
    }

    if (a.locale && b.locale) {
      return a.locale.localeCompare(b.locale);
    }

    return 0;
  });
  return app.eventos.values;
}

export default sortEventos;
